import React from 'react'
import '../layouts/full-stack-dev-go-food.scss'

const ProgramManager = () => (
  <div className="container">
    <div className="content-wrapper">
      <div className="content job-posting-section">
        <div className="section-wrapper">
          <div className="section go-food-section">
            <div className="posting-headline">
              <h2>On-site interviews: What to expect</h2>
            </div>
          </div>
        </div>
        <div className="section-wrapper">
          <div className="section go-food-section">
            <p>
              We’re excited you’re coming to interview at Gojek! Your visit will
              give you an opportunity to meet some more of our team and get a
              feel for what it’s like to work here. We’ve put together this
              short guide to give you an idea of what your day will involve.
              Like many things at a fast-growing startup, we’re constantly
              improving and iterating on how we interview, so if you have any
              comments, concerns, or questions, please let us know. We look
              forward to seeing you soon.
            </p>
            <div>
              <h5 className="text-success">Some general tips</h5>
            </div>
            <p>
              Many of the interviews included during your on-site visit are
              designed to help us understand how you might approach common
              scenarios encountered at Gojek. We’ve designed our case studies to
              reflect the types of problems you may have to solve, but the
              projects and topics are fictional. This is to ensure that we
              evaluate you based on your Program Manager skill-set and not based
              on your knowledge of Gojek. You’ll always have the opportunity to
              ask us questions about anything you don’t understand.
            </p>
            <p>
              Program management at Gojek is cross-functional by nature. To that
              end, we’ve designed an interview loop that will allow you to meet
              with various teams including Program Management, and
              cross-functional stakeholders.
            </p>

            <p>
              Prior to your visit, we will provide you with a tentative
              schedule, including interview times and interviewer details (name
              and team/role).
            </p>
          </div>
          <div className="section go-food-section">
            <div>
              <h3 className="description-heading">HOW TO PREPARE</h3>
              <ul className="posting-requirements plain-list">
                <ul className="go-food-list">
                  <li>
                    Take a look at our{' '}
                    <a href="https://blog.gojekengineering.com/" target="_blank">
                      blog.
                    </a>
                  </li>
                  <li>
                    Read through our product services at{' '}
                    <a href="https://www.gojek.com/" target="_blank">
                      www.go-jek.com
                    </a>{' '}
                    so that you have a basic understanding of what each of our
                    products do.
                  </li>
                  <li>
                    Read about Gojek’s
                    <a href="https://www.gojek.com/about/" target="_blank">
                      &nbsp;mission and our three pillars.
                    </a>
                  </li>
                  <li>
                    Get your whiteboard marker ready! We’ll have a few
                    discussions where you work through problems with the
                    interviewer on the whiteboard; for these, the intent is to
                    evaluate how you solve problems and collaborate with
                    teammates, rather than your Gojek expertise.
                  </li>
                  <li>
                    Consider what's important to you in a job, what you'd like
                    to see in a work environment, how you partner with
                    colleagues and managers, and what you'd like to avoid. Feel
                    free to ask about company goals, team organization, Gojek’s
                    values, etc
                  </li>
                  <li>
                    Take some time to review the job description and think of
                    how your past experience relates to this role. You might
                    find it useful to come up with a few flexible examples of
                    projects you've worked on in the past. Examples of past
                    experience and work often come up during interview
                    conversations, and often it's easiest to draw upon a few
                    vetted examples instead of coming up with something on the
                    spot! (Please don’t discuss anything that your previous or
                    current employers would consider confidential).
                  </li>
                  <li>
                    Be well-rested and ready to think on your feet, as some of
                    the interviews will require you to problem-solve in real
                    time.
                  </li>
                </ul>
              </ul>
            </div>
          </div>
          <div>
            <h5 className="text-success">What we evaluate</h5>
            <p>
              We want you to have the opportunity to present your best self. If
              you’re considering what to emphasize, these are our top five
              priorities:
            </p>
          </div>
          <div className="section go-food-section">
            <div>
              <ul>
                <li>
                  <b>Leadership</b>
                  <ul className="go-food-list">
                    <li>
                      Are you able to provide oversight &amp; guidance to avoid
                      slippage, build a strong pulse &amp; feedback loop on
                      organizational needs, and lead the planning, execution
                      &amp; budgeting of strategic initiatives in partnership
                      with department heads?
                    </li>
                  </ul>
                </li>
                <li>
                  <b>Execution</b>
                  <ul className="go-food-list">
                    <li>
                      Are you able to set project priorities and sequencing,
                      milestone planning, resource allocation, and hiring/talent
                      needs to execute against business area objectives? Are you
                      able to encourage team members to deliver on their
                      commitments and ensure corrective measures are taken on
                      time to bring the project back on track?
                    </li>
                  </ul>
                </li>
                <li>
                  <b>Communication</b>
                  <ul className="go-food-list">
                    <li>
                      Are you able to address questions and concerns from
                      stakeholders? Effectively communicate and roll out
                      complex, high-impact programs that impact a large number
                      of stakeholders e.g. performance management? Are you able
                      to streamline communication channels for easy &amp;
                      effective information sharing?
                    </li>
                  </ul>
                </li>
                <li>
                  <b>Development</b>
                  <ul className="go-food-list">
                    <li>
                      Are you able to implement an effective system of project
                      status tracking and coordinate &amp; set up focus groups
                      for feedback. Are you able to share the successful
                      practices, frameworks, tools &amp; lessons learned by your
                      team back to the larger company?
                    </li>
                  </ul>
                </li>
                <li>
                  <b>Exploration</b>
                  <ul className="go-food-list">
                    <li>
                      Are you able to audit process effectiveness through
                      qualitative &amp; quantitative means and compare our
                      processes against the market? Are you able to identify
                      opportunities for business growth or identify
                      opportunities for more PgM work?
                    </li>
                  </ul>
                </li>
              </ul>
            </div>
          </div>
          <div>
            <h5 className="text-success">How long will the interview take?</h5>
            <p>
              While some candidates may have a shorter day with us, it’s best to
              plan to be at Gojek for about five hours. If you have any time
              constraints, please be sure to let your recruiter know.
            </p>
          </div>
          <div>
            <h5 className="text-success">Wondering what to wear?</h5>
            <p>
              Many people at Gojek show up to work in jeans and a t-shirt (or
              button down/blouse). We recommend simply wearing what you're most
              accustomed to wearing, because that’s what we do!
            </p>
          </div>
        </div>
        <div>
          <h5 className="text-success">Additional notes and accommodations</h5>
          <p className="mb-1">
            <b>Breaks:</b>
          </p>
          <ul className="posting-requirements plain-list ml-3">
            <ul className="go-food-list">
              <li>
                We will do our best to provide a 15 minute break and/or
                lunch/coffee break during your time on site, especially if
                you'll be here for more than 3 hours. That said, we are happy to
                accommodate additional breaks or fewer breaks, depending on your
                needs and preferences, just let us know!
              </li>
              <li>
                To recent mothers, we are happy to accommodate any breaks needed
                during your on-site visit. Please be sure to inform your
                recruiting partner, so we can make the arrangements that are
                best for you!
              </li>
            </ul>
          </ul>

          <p className="mb-1">
            <b>Accommodations:</b>
          </p>
          <ul className="posting-requirements plain-list ml-3">
            <ul className="go-food-list">
              <li>
                If you need any accommodations to make your interview process a
                success, please don’t hesitate to ask.
              </li>
            </ul>
          </ul>
        </div>
        <p>
          Thank you again for taking the time to come in to Gojek. We want to
          make your experience as pleasant as possible, so please let your
          recruiter know if there is anything else we can do to set you up for
          success. Our team is very excited to meet you!
        </p>
      </div>
    </div>
  </div>
)

export default ProgramManager



